:root {
  --main-font: "Muli", sans-serif;
  --header-font: "Lora", serif;
  --brand-color: #f72464;
  --hover-color: #d10c47;
  --main-font-color: #52575d;
  --header-font-color: #222222;
  --subtitle-color: #919191;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  font-family: var(--main-font);
  color: var(--main-font-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--header-font);
  color: var(--header-font-color);
}

h1 {
  font-family: var(--header-font);
  font-weight: normal;
  font-size: 8rem;
  line-height: 1.25;
  text-transform: capitalize;
  margin-bottom: 0.5em;
}

h2 {
  font-size: 3.5rem;
  font-weight: normal;
  text-align: center;
}

h3 {
  font-size: 1.2rem;
  font-weight: 400;
  text-align: center;
  color: var(--subtitle-color);
}

.page-wrapper {
  position: relative;
  min-width: 420px;
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff;
  overflow: hidden;
}

.container {
  position: relative;
  max-width: 1180px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.header__top {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 25px;
}

.header__content {
  position: relative;
  min-height: 700px;
}

.header__content-main {
  max-width: 700px;
  padding-top: 150px;
}
.header__content-main .btn {
  min-width: 180px;
}

.header__subtitle {
  font-family: var(--main-font);
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.67;
  margin-bottom: 55px;
}

.header-image {
  position: absolute;
  top: 0px;
  right: 30px;
  bottom: 0px;
  max-width: 800px;
  overflow: hidden;
}

@media all and (max-width: 585px) {
  .header__top {
    flex-direction: column;
  }

  .basket {
    margin-left: 0px;
    position: absolute;
    left: 0px;
    top: 0px;
  }
}
.logo {
  margin-right: 60px;
  text-transform: uppercase;
  font-size: 2.2rem;
  font-weight: bold;
  transition: all 0.3s;
}
.logo:active {
  transform: scale(0.9);
}

.logo__link {
  text-decoration: none;
  color: #041122;
  transition: all 0.3s;
}
.logo__link:hover, .logo__link:focus {
  color: var(--brand-color);
  outline: none;
}

.main-nav__list {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.main-nav__list-item {
  margin-right: 25px;
}
.main-nav__list-item:active {
  transform: scale(0.9);
}

.main-nav__link {
  font-size: 1.5rem;
  font-weight: normal;
  text-decoration: none;
  color: var(--main-font-color);
  transition: all 0.3s;
}
.main-nav__link:hover, .main-nav__link:focus {
  color: var(--brand-color);
  outline: none;
}

.basket {
  position: relative;
  margin-left: auto;
  font-size: 3rem;
}
.basket:active {
  transform: scale(0.9);
}

.basket__link {
  text-decoration: none;
  color: var(--main-font-color);
  transition: all 0.3s;
}
.basket__link:hover, .basket__link:focus {
  color: var(--brand-color);
  outline: none;
}
.basket__link:active {
  transform: scale(0.9);
}

.count {
  display: block;
  position: absolute;
  right: -12px;
  bottom: -8px;
  padding: 5px 8px;
  border-radius: 100%;
  font-size: 1.2rem;
  font-weight: bold;
  color: #fff;
  background-color: var(--brand-color);
}

@media all and (max-width: 585px) {
  .basket {
    width: 25px;
    position: absolute;
    right: 0px;
    top: 10px;
  }
}
.btn {
  /* default for <button>, but useful for <a> */
  display: inline-block;
  font-size: 2rem;
  text-transform: capitalize;
  font-weight: normal;
  text-align: center;
  text-decoration: none;
  /* invisible border (will be colored on hover/focus) */
  border: solid 1px transparent;
  border-radius: 4px;
  /* size comes from text & padding (no width/height) */
  padding: 15px 20px;
  outline: none;
  /* make sure colors have enough contrast! */
  color: #fff;
  background-color: var(--brand-color);
  cursor: pointer;
  animation: all 0.6s;
}
.btn:hover {
  background-color: var(--hover-color);
}
.btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 105, 180, 0.5), 0 0 0 1.5px rgba(255, 105, 180, 0.5);
}
.btn:active {
  transform: translateY(1px);
  filter: saturate(150%);
}

.btn.btn--border {
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 600;
  color: #919191;
  background: transparent;
  border: 2px solid var(--brand-color);
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.btn.btn--border:hover {
  background: transparent;
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 105, 180, 0.5), 0 0 0 1.5px rgba(255, 105, 180, 0.5);
}

.new-products {
  padding-top: 100px;
}

.card {
  margin-top: 100px;
  padding-bottom: 100px;
  text-align: center;
}

.card__wrapper {
  margin-bottom: 70px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card__item {
  position: relative;
  width: 265px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 50px;
}

.card__cover {
  position: relative;
  margin-bottom: 20px;
  overflow: hidden;
}
.card__cover img {
  object-fit: cover;
}

.card__buttons {
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  height: 0%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 40px;
  padding-right: 40px;
  background: rgba(0, 0, 0, 0.5);
  display: none;
  opacity: 0;
}

.card__cover:hover .card__buttons,
.card__cover:focus .card__buttons {
  display: flex;
  animation: showButton 0.3s;
  animation-fill-mode: forwards;
}

@keyframes showButton {
  0% {
    opacity: 0;
    height: 0%;
  }
  100% {
    opacity: 1;
    height: 100%;
  }
}
.card__title {
  font-size: 1.8rem;
  font-weight: normal;
  text-align: center;
  margin-bottom: 20px;
}

.card__price {
  font-size: 1.3rem;
  font-weight: bold;
  color: var(--brand-color);
  text-align: center;
}

.card__old-price {
  display: inline-block;
  color: #838b8f;
  text-decoration: line-through;
  margin-right: 5px;
}

.you-love {
  min-height: 185px;
  background-image: url(../images/you-love/bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.you-love .container {
  min-height: 185px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
.you-love .btn {
  min-width: 225px;
}

.you-love__title {
  font-style: 6rem;
  font-weight: normal;
  color: #fff;
}

.fetured-products {
  padding-top: 100px;
}

.brands {
  min-height: 185px;
  background: url(../images/brands/brands_BG.jpg) no-repeat;
  background-position: center;
  background-size: cover;
  margin-bottom: 100px;
}

.brands__list {
  min-height: 185px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.brands__item {
  margin-right: 10px;
  margin-left: 10px;
}

.brands__logo {
  width: 160px;
  height: 80px;
  object-fit: cover;
}

.blog {
  text-align: center;
  margin-bottom: 100px;
}

.blog__title {
  margin-bottom: 10px;
}

.blog__posts-wrapper {
  margin-top: 100px;
  margin-bottom: 60px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.blog__post {
  max-width: 550px;
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  margin-right: 20px;
}
.blog__post img {
  margin-right: 20px;
  widows: 165px;
  height: 165px;
  object-fit: cover;
}

.blog__posts-title {
  font-size: 1.8rem;
  font-weight: bold;
  line-height: 1.67;
}

.blog__post-text {
  font-size: 1.5rem;
  line-height: 1.67;
  color: #898989;
  margin-bottom: 10px;
}

.blog__link {
  position: relative;
  display: inline-block;
  font-size: 15px;
  color: #222222;
  text-decoration: none;
}
.blog__link:hover, .blog__link:focus {
  outline: none;
  color: var(--brand-color);
  text-decoration: underline;
}
.blog__link:active {
  text-decoration: none;
}
.blog__link::after {
  display: block;
  position: absolute;
  content: ">";
  top: 0px;
  right: -15px;
  font-size: 15px;
  color: var(--brand-color);
}

.join {
  text-align: center;
  padding-bottom: 100px;
}

.join__title {
  margin-bottom: 35px;
}

.join__subtitle {
  font-size: 1.5rem;
  line-height: 1.67;
  color: #898989;
  max-width: 550px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}

.subscribe-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.subscribe-form__email {
  margin-right: 10px;
  width: 250px;
  height: 50px;
  border-radius: 4px;
  outline: none;
  border: 1px solid var(--brand-color);
  background: transparent;
  text-align: center;
  font-size: 1.5rem;
  color: #c4c4c4;
  margin-bottom: 10px;
}
.subscribe-form__email:focus {
  box-shadow: 0 0 0 3px rgba(255, 105, 180, 0.5), 0 0 0 1.5px rgba(255, 105, 180, 0.5);
}
.subscribe-form .btn {
  min-width: 125px;
  height: 50px;
  margin-bottom: 10px;
}

.footer {
  text-align: center;
  background: #000;
  padding-top: 60px;
  padding-bottom: 60px;
}

.footer__links {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
}

.footer__link-item {
  margin-right: 35px;
}

.footer__link {
  text-decoration: none;
  color: #fff;
  outline: none;
}
.footer__link:hover, .footer__link:focus {
  color: var(--brand-color);
  text-decoration: underline;
}
.footer__link:active {
  text-decoration: none;
}

.social {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.social__item {
  margin-right: 25px;
  margin-bottom: 25px;
  transition: all 0.3s;
}
.social__item:hover, .social__item:focus {
  transform: scale(1.2);
}
.social__item:active {
  transform: scale(1);
}

.social__link {
  text-decoration: none;
  outline: none;
  color: #fff;
  font-size: 25px;
}
.social__link:hover, .social__link:focus {
  color: var(--brand-color);
}

.copyright {
  font-style: 1.2rem;
  font-weight: normal;
  line-height: 2em;
  color: #898989;
}